___CSS_LOADER_EXPORT___.push([module.id, `* {
  box-sizing: border-box;
  font-size: 1.2rem;
  line-height: 1.5;
  /* https://css-tricks.com/snippets/css/system-font-stack/ below: */
  font-family: system-ui, "Segoe UI", Roboto, Helvetica, Arial, sans-serif,
    "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
}

body {
  display: flex;
  justify-content: center;
  background-color: #f8e2e4;
}

.app-container {
  display: flex;
  flex-direction: column;
  /* justify-content: center; */
  align-items: center;
  gap: 10px;
  width: 550px;
  background-color: #f1c5ca;
  padding: 10px;
  margin: 10px;
  border-radius: 10px;
}

.current-container {
  width: 500px;
  display: flex;
  gap: 20px;
  align-items: center;
  border: 1px solid #be2d3b;
  border-radius: 10px;
  padding: 10px;
}

.temp-toggle div,
.temp-toggle button {
  font-size: 0.7rem;
}

.temp-toggle div {
  width: 70px;
}

.temp-toggle {
  display: flex;
  gap: 10px;
}

button {
  border-radius: 5px;
  border: 1px solid black;
}

input {
  border-radius: 5px;
  border: 1px solid black;
}

.forecast {
  width: 350px;
  display: flex;
  flex-direction: column;
  gap: 10px;
  border: 1px solid #be2d3b;
  border-radius: 10px;
  padding: 10px;
}

.footer-container {
  display: flex;
  gap: 10px;
  align-items: center;
}

.current-container .text,
.footer-container .text {
  width: 250px;
  margin-right: auto;
}

.city,
.date {
  font-weight: bold;
}

.current-container,
.forecast {
  display: none;
}

.temp-f {
  display: none;
}

.gif img {
  border-radius: 10px;
}
